.sales__pitch{
    padding: 100px 0;
    background-image: url('../images/pattern_opacity_06.png');
    background-size: cover;
    background-position: center center;
    &-content{
        h3{
            font-family: $primary-font;
            color: $text-color-dark;
            font-size: 36px;
            font-weight: 600;
            line-height: 45px;
            margin-bottom: 30px;
        }
        p{
            color: $text-color;
            font-size: 18px;
            font-family: $secondary-font;
            line-height: 30px;
        }
    }
}

.customer__reviews{
    padding: 100px 0;
    &-heading{
        h3{
            font-family: $primary-font;
            color: $text-color-dark;
            font-size: 36px;
            font-weight: 600;
            line-height: 45px;
            margin-bottom: 30px;
        }
        p{
            color: $text-color;
            font-size: 18px;
            font-family: $secondary-font;
            line-height: 30px;
            width: 70%;
            margin: 0 auto 50px;
        }
    }
    &-item{
        font-family: $secondary-font;
        background-color: $white;
        border-radius: 6px;
        box-shadow: 0 2px 27px 3px rgba(0,0,0,.06);
        padding: 40px 30px;
        margin-bottom: 50px;
        h5{
            font-family: $primary-font;
            font-weight: 700;
            margin-bottom: 20px;
            color: $text-color-dark;
        }
        h6{
            font-weight: 700;
            color: $text-color-dark;
        }
        p{
            color: $text-color;
            font-size: 15px;
            line-height: 26px;
        }
        .reply{
            padding: 20px 20px 20px 40px;
            background-color: darken($color: $white, $amount: 5);
            border-left: 4px solid $secondary-color;
            h6{
                margin-bottom: 5px;
            }
            p{
                font-size: 14px;
                font-style: italic;
                margin-bottom: 0;
            }
        }
    }
}