.page{
    &__header{
        position: relative;
        padding: 150px 0 200px;
        background-image: $primary-gradient;
        overflow: hidden;
        &-title{
            font-family: $primary-font;
            color: $white;
            font-size: 48px;
            font-weight: 700;
            margin-bottom: 30px;
        }
        &-subtitle{
            font-family: $secondary-font;
            font-size: 18px;
            font-weight: 500;
            color: $white;
            width: 70%;
            margin: 0 auto;
        }
        &-separator{
            position: absolute;
            left: -1%;
            height: auto;
            width: 102%;
            bottom: 0;
            border-bottom: 1px solid #fff;
        }
    }
}